
#root {
    display: flex;
    position: static;
    width: 100%;
    height: 100%;
    align-content: center;
    align-items: center;
    justify-content: center;
}

.bg.light {
    width: 100%;
    height: 100%;
    background: radial-gradient(rgba(255, 255, 0, .1) 10%, rgba(255, 255, 0, 0) 70%);
    transition: filter 500ms linear, opacity 500ms ease-in-out;
    position: absolute;
    opacity: var(--opacity-filter);
}


.nightmare.bg {
    left: 41%;
    top: 10%;
    opacity: 1;
    width: 50%;
    height: 47%;
}

.Game {
    width: 600px;
    height: 600px;
    position: relative;
    left: 32px;
    top: -32px;
}

@media (max-width: 1000px) {
    /*#root *{*/
