    .Game {
        top: 20px;
        width: 100%;
        height: 50%;
    }

    .Game .start-btn {
        left: calc(50% - 144px);
        top: calc(50% - 113px);
    }
}

.start-btn {
    position: absolute;

    left: calc(50% - 200px);
    top: calc(50% - 148px);


}

.loading-btn {
    left: 25%;
    top: 0;
}
